/**
 * File Operation Logger
 * 
 * Comprehensive logging system for tracking all file operations during tests
 * with alert mechanisms for suspicious activities targeting system files.
 * 
 * Features:
 * - Complete filesystem operation monitoring
 * - Real-time threat detection and alerting
 * - Detailed audit trail generation
 * - Suspicious activity pattern recognition
 * - Security incident reporting
 */

const fs = require('fs');
const path = require('path');

class FileOperationLogger {
    constructor(options = {}) {
        this.projectRoot = options.projectRoot || process.cwd();
        this.logFile = path.join(this.projectRoot, 'development/file-operations.log');
        this.alertFile = path.join(this.projectRoot, 'development/security-alerts.log');
        this.auditFile = path.join(this.projectRoot, 'development/file-audit.json');
        
        // Detect test environment for optimized behavior
        this.isTestEnvironment = process.env.NODE_ENV === 'test' || 
                                 process.env.JEST_WORKER_ID !== undefined ||
                                 options.testMode === true;
        
        // Configuration with test mode optimizations
        this.config = {
            enableRealTimeAlerts: this.isTestEnvironment ? false : (options.enableRealTimeAlerts !== false),
            enableAuditTrail: this.isTestEnvironment ? false : (options.enableAuditTrail !== false),
            enableThreatDetection: options.enableThreatDetection !== false,
            enableConsoleLogging: this.isTestEnvironment ? false : (options.enableConsoleLogging !== false),
            alertThreshold: options.alertThreshold || 5, // Suspicious operations per minute
            maxLogSize: options.maxLogSize || 10 * 1024 * 1024, // 10MB
            ...options.config
        };
        
        // Runtime state
        this.operations = [];
        this.alerts = [];
        this.suspiciousPatterns = new Map();
        this.sessionId = this.generateSessionId();
        this.startTime = Date.now();
        
        // Define critical system paths that should trigger alerts
        this.criticalPaths = [
            'node_modules/exit',
            'node_modules/jest-worker',
            'node_modules/jest',
            '/usr/',
            '/bin/',
            '/lib/',
            '/System/',
            'package-lock.json',
            'yarn.lock',
            '.git/',
            'node_modules/.bin'
        ];
        
        // Define suspicious operation patterns
        this.suspiciousPatterns = new Set([
            'JSON_TO_JS_FILE',          // Writing JSON data to .js files
            'SYSTEM_FILE_MODIFICATION', // Modifying system files
            'RAPID_FILE_OPERATIONS',    // Too many operations in short time
            'BINARY_CORRUPTION',        // Writing binary data to text files
            'PERMISSION_ESCALATION',    // Attempting to modify protected files
            'STEGANOGRAPHY_ATTEMPT',    // Hidden data in files
            'TIME_BOMB_PATTERN'         // Time-based malicious operations
        ]);
        
        this.initializeLogger();
    }
    
    /**
     * Initialize the logging system
     */
    initializeLogger() {
        try {
            // Ensure log directory exists
            const logDir = path.dirname(this.logFile);
            if (!fs.existsSync(logDir)) {
                fs.mkdirSync(logDir, { recursive: true });
            }
            
            // Write session header
            this.writeLogHeader();
            
            // Only log initialization in non-test environments
            if (!this.isTestEnvironment) {
                this.log('FileOperationLogger initialized', {
                    sessionId: this.sessionId,
                    projectRoot: this.projectRoot,
                    criticalPathsCount: this.criticalPaths.length
                });
            }
            
        } catch (error) {
            console.error('Failed to initialize FileOperationLogger:', error.message);
        }
    }
    
    /**
     * Log a file operation with threat analysis
     */
    logOperation(operation, filePath, data = null, additionalInfo = {}) {
        const timestamp = new Date().toISOString();
        const normalizedPath = path.resolve(filePath);
        
        const operationData = {
            id: this.generateOperationId(),
            sessionId: this.sessionId,
            timestamp,
            operation,
            filePath: normalizedPath,
            relativePath: path.relative(this.projectRoot, normalizedPath),
            dataSize: data ? Buffer.byteLength(data, 'utf8') : 0,
            dataType: this.detectDataType(data),
            stackTrace: this.captureStackTrace(),
            threatLevel: 'LOW',
            suspicious: false,
            ...additionalInfo
        };
        
        // Perform threat analysis
        this.analyzeThreat(operationData, data);
        
        // Add to operations log
        this.operations.push(operationData);
        
        // Write to log file
        this.writeOperationToLog(operationData);
        
        // Check for alerts
        if (operationData.suspicious && this.config.enableRealTimeAlerts) {
            this.generateAlert(operationData, data);
        }
        
        // Cleanup old operations if memory gets too large
        if (this.operations.length > 10000) {
            this.operations = this.operations.slice(-5000);
        }
        
        return operationData.id;
    }
    
    /**
     * Analyze operation for threats and suspicious patterns
     */
    analyzeThreat(operationData, data) {
        const threats = [];
        
        // Check if targeting critical system paths
        if (this.isCriticalPath(operationData.filePath)) {
            threats.push('SYSTEM_FILE_MODIFICATION');
            operationData.threatLevel = 'HIGH';
        }
        
        // Check for JSON contamination attempts
        if (this.isJsonContamination(operationData, data)) {
            threats.push('JSON_TO_JS_FILE');
            operationData.threatLevel = 'CRITICAL';
        }
        
        // Check for rapid operation patterns
        if (this.isRapidOperationPattern(operationData)) {
            threats.push('RAPID_FILE_OPERATIONS');
            operationData.threatLevel = Math.max(operationData.threatLevel, 'MEDIUM');
        }
        
        // Check for binary corruption attempts
        if (this.isBinaryCorruption(operationData, data)) {
            threats.push('BINARY_CORRUPTION');
            operationData.threatLevel = 'HIGH';
        }
        
        // Check for permission escalation attempts
        if (this.isPermissionEscalation(operationData)) {
            threats.push('PERMISSION_ESCALATION');
            operationData.threatLevel = 'HIGH';
        }
        
        // Check for steganography attempts
        if (this.isSteganographyAttempt(operationData, data)) {
            threats.push('STEGANOGRAPHY_ATTEMPT');
            operationData.threatLevel = 'MEDIUM';
        }
        
        // Check for time bomb patterns
        if (this.isTimeBombPattern(operationData, data)) {
            threats.push('TIME_BOMB_PATTERN');
            operationData.threatLevel = 'CRITICAL';
        }
        
        operationData.threats = threats;
        operationData.suspicious = threats.length > 0;
        
        // Update threat level based on number of threats
        if (threats.length >= 3) {
            operationData.threatLevel = 'CRITICAL';
        } else if (threats.length >= 2) {
            operationData.threatLevel = 'HIGH';
        }
    }
    
    /**
     * Generate security alert for suspicious operation
     */
    generateAlert(operationData, data) {
        const alert = {
            id: this.generateAlertId(),
            sessionId: this.sessionId,
            timestamp: new Date().toISOString(),
            threatLevel: operationData.threatLevel,
            operation: operationData,
            threats: operationData.threats,
            recommendation: this.generateRecommendation(operationData),
            dataPreview: data ? data.substring(0, 200) + '...' : null,
            context: this.gatherSecurityContext(operationData)
        };
        
        this.alerts.push(alert);
        
        // Write alert to file
        this.writeAlert(alert);
        
        // Console alert for immediate attention
        this.outputConsoleAlert(alert);
        
        return alert.id;
    }
    
    /**
     * Generate security context for alert
     */
    gatherSecurityContext(operationData) {
        const recentOperations = this.operations
            .filter(op => Date.now() - new Date(op.timestamp).getTime() < 60000) // Last minute
            .length;
            
        const similarOperations = this.operations
            .filter(op => 
                op.operation === operationData.operation &&
                Date.now() - new Date(op.timestamp).getTime() < 300000 // Last 5 minutes
            ).length;
            
        return {
            recentOperations,
            similarOperations,
            sessionDuration: Date.now() - this.startTime,
            totalOperationsInSession: this.operations.length,
            previousAlerts: this.alerts.length
        };
    }
    
    /**
     * Generate recommendation based on threat analysis
     */
    generateRecommendation(operationData) {
        const recommendations = [];
        
        if (operationData.threats.includes('SYSTEM_FILE_MODIFICATION')) {
            recommendations.push('Immediately verify the integrity of system files');
            recommendations.push('Consider restoring from backup if corruption detected');
        }
        
        if (operationData.threats.includes('JSON_TO_JS_FILE')) {
            recommendations.push('Check for JSON contamination in JavaScript files');
            recommendations.push('Restore original file contents from backup');
        }
        
        if (operationData.threats.includes('RAPID_FILE_OPERATIONS')) {
            recommendations.push('Investigate potential automated attack or malware');
            recommendations.push('Monitor system resources for unusual activity');
        }
        
        if (operationData.threats.includes('PERMISSION_ESCALATION')) {
            recommendations.push('Verify file permissions and access controls');
            recommendations.push('Check for unauthorized privilege elevation');
        }
        
        if (operationData.threatLevel === 'CRITICAL') {
            recommendations.push('IMMEDIATE ACTION REQUIRED: Stop all test execution');
            recommendations.push('Perform full system integrity check');
            recommendations.push('Consider isolating the system from network');
        }
        
        return recommendations;
    }
    
    /**
     * Save comprehensive audit report
     */
    async saveAuditReport() {
        if (!this.config.enableAuditTrail) {
            return;
        }
        
        try {
            const auditReport = {
                sessionId: this.sessionId,
                startTime: new Date(this.startTime).toISOString(),
                endTime: new Date().toISOString(),
                duration: Date.now() - this.startTime,
                summary: {
                    totalOperations: this.operations.length,
                    suspiciousOperations: this.operations.filter(op => op.suspicious).length,
                    alerts: this.alerts.length,
                    criticalAlerts: this.alerts.filter(a => a.threatLevel === 'CRITICAL').length,
                    highThreatAlerts: this.alerts.filter(a => a.threatLevel === 'HIGH').length
                },
                operations: this.operations,
                alerts: this.alerts,
                threatAnalysis: this.generateThreatAnalysis(),
                recommendations: this.generateSessionRecommendations()
            };
            
            await fs.promises.writeFile(this.auditFile, JSON.stringify(auditReport, null, 2));
            
            this.log('Audit report saved', {
                auditFile: this.auditFile,
                operationsCount: this.operations.length,
                alertsCount: this.alerts.length
            });
            
        } catch (error) {
            this.log('Failed to save audit report', { error: error.message }, 'error');
        }
    }
    
    // ========================================================================
    // THREAT DETECTION METHODS
    // ========================================================================
    
    /**
     * Check if path is critical system path
     */
    isCriticalPath(filePath) {
        return this.criticalPaths.some(criticalPath => 
            filePath.includes(criticalPath)
        );
    }
    
    /**
     * Detect JSON contamination attempts
     */
    isJsonContamination(operationData, data) {
        if (!data || typeof data !== 'string') {
            return false;
        }
        
        // Check if writing JSON-like data to JavaScript files
        if (operationData.filePath.endsWith('.js') && 
            (data.trim().startsWith('{') || data.trim().startsWith('['))) {
            return true;
        }
        
        // Check for specific contamination patterns
        const contaminationPatterns = [
            /"tasks":\s*\[/,
            /"project":\s*"/,
            /"execution_count":/,
            /"review_strikes":/
        ];
        
        return contaminationPatterns.some(pattern => pattern.test(data));
    }
    
    /**
     * Detect rapid operation patterns
     */
    isRapidOperationPattern(_operationData) {
        const oneMinuteAgo = Date.now() - 60000;
        const recentOperations = this.operations.filter(op => 
            new Date(op.timestamp).getTime() > oneMinuteAgo
        );
        
        return recentOperations.length > this.config.alertThreshold;
    }
    
    /**
     * Detect binary corruption attempts
     */
    isBinaryCorruption(operationData, data) {
        if (!data || typeof data !== 'string') {
            return false;
        }
        
        // Check for binary data in text files
        if (operationData.filePath.match(/\.(js|json|md|txt|log)$/)) {
            // Look for null bytes or high-byte characters
            if (data.includes('\0') || /[\x80-\xFF]/.test(data)) {
                return true;
            }
        }
        
        return false;
    }
    
    /**
     * Detect permission escalation attempts
     */
    isPermissionEscalation(operationData) {
        // Check if attempting to write to protected directories
        const protectedPaths = [
            '/usr/bin',
            '/usr/lib',
            '/System',
            'node_modules/.bin'
        ];
        
        return protectedPaths.some(protectedPath => 
            operationData.filePath.includes(protectedPath)
        );
    }
    
    /**
     * Detect steganography attempts
     */
    isSteganographyAttempt(operationData, data) {
        if (!data || typeof data !== 'string') {
            return false;
        }
        
        // Look for hidden data patterns
        const steganographyPatterns = [
            /<!--.*-->/s,  // HTML comments with suspicious content
            /\/\*.*\*\//s, // Block comments with unusual content
            /\u200B|\u200C|\u200D|\uFEFF/,  // Zero-width characters
            /\x20{10,}/  // Excessive whitespace
        ];
        
        return steganographyPatterns.some(pattern => pattern.test(data));
    }
    
    /**
     * Detect time bomb patterns
     */
    isTimeBombPattern(operationData, data) {
        if (!data || typeof data !== 'string') {
            return false;
        }
        
        // Look for time-based trigger patterns
        const timeBombPatterns = [
            /setTimeout.*\d{4,}/,  // Long timeouts
            /setInterval.*\d{4,}/, // Long intervals
            /Date\.now\(\).*[+\-*/].*\d{6,}/, // Date calculations
            /new Date.*202[4-9]/, // Future dates
            /process\.nextTick.*setTimeout/ // Nested async patterns
        ];
        
        return timeBombPatterns.some(pattern => pattern.test(data));
    }
    
    // ========================================================================
    // UTILITY METHODS
    // ========================================================================
    
    /**
     * Detect data type from content
     */
    detectDataType(data) {
        if (!data) return 'empty';
        if (typeof data !== 'string') return 'binary';
        
        const trimmed = data.trim();
        if (trimmed.startsWith('{') && trimmed.endsWith('}')) return 'json';
        if (trimmed.startsWith('[') && trimmed.endsWith(']')) return 'json_array';
        if (trimmed.includes('function') || trimmed.includes('=>')) return 'javascript';
        if (trimmed.includes('<html') || trimmed.includes('<!DOCTYPE')) return 'html';
        if (/^[\w-]+:/.test(trimmed)) return 'yaml';
        
        return 'text';
    }
    
    /**
     * Capture stack trace for operation source
     */
    captureStackTrace() {
        const stack = new Error().stack;
        return stack ? stack.split('\n').slice(2, 6).map(line => line.trim()) : [];
    }
    
    /**
     * Generate unique session ID
     */
    generateSessionId() {
        return `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
    }
    
    /**
     * Generate unique operation ID
     */
    generateOperationId() {
        return `op_${Date.now()}_${Math.random().toString(36).substr(2, 6)}`;
    }
    
    /**
     * Generate unique alert ID
     */
    generateAlertId() {
        return `alert_${Date.now()}_${Math.random().toString(36).substr(2, 6)}`;
    }
    
    /**
     * Write log header
     */
    writeLogHeader() {
        const header = `
=================================================================
FILE OPERATION LOGGER SESSION
=================================================================
Session ID: ${this.sessionId}
Start Time: ${new Date().toISOString()}
Project Root: ${this.projectRoot}
Process ID: ${process.pid}
Node Version: ${process.version}
Platform: ${process.platform}
=================================================================

`;
        fs.appendFileSync(this.logFile, header);
    }
    
    /**
     * Write operation to log file
     */
    writeOperationToLog(operationData) {
        try {
            const logEntry = `[${operationData.timestamp}] ${operationData.operation} | ${operationData.relativePath} | ${operationData.threatLevel} | ${operationData.suspicious ? 'SUSPICIOUS' : 'CLEAN'}\n`;
            
            // Check log file size and rotate if necessary
            if (fs.existsSync(this.logFile)) {
                const stats = fs.statSync(this.logFile);
                if (stats.size > this.config.maxLogSize) {
                    this.rotateLogFile();
                }
            }
            
            fs.appendFileSync(this.logFile, logEntry);
        } catch (error) {
            console.error('Failed to write operation to log:', error.message);
        }
    }
    
    /**
     * Write security alert
     */
    writeAlert(alert) {
        try {
            const alertEntry = {
                timestamp: alert.timestamp,
                level: alert.threatLevel,
                threats: alert.threats,
                file: alert.operation.relativePath,
                recommendation: alert.recommendation
            };
            
            const logLine = JSON.stringify(alertEntry) + '\n';
            fs.appendFileSync(this.alertFile, logLine);
        } catch (error) {
            console.error('Failed to write alert:', error.message);
        }
    }
    
    /**
     * Output console alert for immediate attention
     */
    outputConsoleAlert(alert) {
        const emoji = {
            CRITICAL: '🚨',
            HIGH: '⚠️',
            MEDIUM: '⚠️',
            LOW: 'ℹ️'
        };
        
        console.log(`\n${emoji[alert.threatLevel]} SECURITY ALERT [${alert.threatLevel}]`);
        console.log(`Operation: ${alert.operation.operation}`);
        console.log(`File: ${alert.operation.relativePath}`);
        console.log(`Threats: ${alert.threats.join(', ')}`);
        console.log(`Recommendations:`);
        alert.recommendation.forEach(rec => console.log(`  • ${rec}`));
        console.log('');
    }
    
    /**
     * Rotate log file when it gets too large
     */
    rotateLogFile() {
        try {
            const timestamp = new Date().toISOString().replace(/:/g, '-');
            const rotatedFile = this.logFile.replace('.log', `-${timestamp}.log`);
            fs.renameSync(this.logFile, rotatedFile);
        } catch (error) {
            console.warn('Failed to rotate log file:', error.message);
        }
    }
    
    /**
     * Generate threat analysis summary
     */
    generateThreatAnalysis() {
        const threatCounts = {};
        const threatLevelCounts = { CRITICAL: 0, HIGH: 0, MEDIUM: 0, LOW: 0 };
        
        this.operations.forEach(op => {
            threatLevelCounts[op.threatLevel]++;
            if (op.threats) {
                op.threats.forEach(threat => {
                    threatCounts[threat] = (threatCounts[threat] || 0) + 1;
                });
            }
        });
        
        return {
            threatCounts,
            threatLevelCounts,
            riskScore: this.calculateRiskScore(),
            topThreats: Object.entries(threatCounts)
                .sort(([,a], [,b]) => b - a)
                .slice(0, 5)
        };
    }
    
    /**
     * Calculate overall risk score for session
     */
    calculateRiskScore() {
        let score = 0;
        
        this.operations.forEach(op => {
            switch (op.threatLevel) {
                case 'CRITICAL': score += 10; break;
                case 'HIGH': score += 5; break;
                case 'MEDIUM': score += 2; break;
                case 'LOW': score += 1; break;
            }
        });
        
        return Math.min(100, score); // Cap at 100
    }
    
    /**
     * Generate session recommendations
     */
    generateSessionRecommendations() {
        const recommendations = [];
        const riskScore = this.calculateRiskScore();
        
        if (riskScore > 50) {
            recommendations.push('HIGH RISK SESSION: Perform full security audit');
            recommendations.push('Review all file modifications for malicious content');
            recommendations.push('Consider reverting to known-good backup');
        } else if (riskScore > 20) {
            recommendations.push('Moderate risk detected: Review suspicious operations');
            recommendations.push('Verify integrity of critical system files');
        } else {
            recommendations.push('Low risk session: Standard monitoring sufficient');
        }
        
        return recommendations;
    }
    
    /**
     * Simple logging method
     */
    log(message, data = {}, level = 'info') {
        // Only log to console if console logging is enabled
        if (this.config.enableConsoleLogging) {
            if (level === 'error') {
                console.error(`[FileOperationLogger] ${message}`, data);
            } else {
                console.log(`[FileOperationLogger] ${message}`, Object.keys(data).length > 0 ? data : '');
            }
        }
    }
}

module.exports = FileOperationLogger;